    }
  }
  img {
    width: 100%;
    height: 100%;
  }
  &.circle {
    position: relative;
    width: 165px;
    height: 165px;
    border-radius: 50%;
    .img {
      position: relative;
      width: 165px;
      height: 165px;
      border-radius: 50%;
      &:before {
        position: absolute;
        display: block;
        content: '';
        width: 100%;
        height: 100%;
        border-radius: 50%;
        box-shadow: inset 0 0 0 160px rgba(100, 121, 140, .5);
        -webkit-transition: all 0.35s ease-in-out;
        -moz-transition: all 0.35s ease-in-out;
        transition: all 0.35s ease-in-out;
      }
      img {
        border-radius: 50%;
      }
    }
    .info {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      text-align: center;
      border-radius: 50%;
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden;
    }
  }
  &.square {
    position: relative;
    width: 316px;
    height: 216px;
    border: 8px solid #fff;
    box-shadow: 1px 1px 3px rgba(100, 121, 140, 0.3);
    .info {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      text-align: center;
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden;
    }
  }
  &.circle {
    &.effect1 {
      .spinner {
        width: 165px;
        height: 165px;
        border: 10px solid #ecab18;
        border-right-color: #1ad280;
        border-bottom-color: #1ad280;
        border-radius: 50%;
        -webkit-transition: all 0.8s ease-in-out;
        -moz-transition: all 0.8s ease-in-out;
        transition: all 0.8s ease-in-out;
      }
      .img {
        position: absolute;
        top: 10px;
        bottom: 10px;
        left: 10px;
        right: 10px;
        width: auto;
        height: auto;
        &:before {
          display: none;
        }
      }
      &.colored .info {
        background: #1a4a72;
        background: rgba(26, 74, 114, 0.6);
      }
      .info {
